import uniPopup from "@/components/uni-popup/uni-popup.vue"
import {
	uniCollapse,
	uniCollapseItem
} from "@dcloudio/uni-ui"

export default {
	data() {
		return {
			myInfo: {
				leaseStartDate: "",
				leaseEndDate: "",
				buildingNumber: "",
				deposit: "",
				status: ""
			}

		}
	},

	onLoad() {
		var that = this;
		var loginName = uni.getStorageSync('loginName');
		uni.request({
			url: 'http://192.168.1.104:8080/gongyv_manage/api/fetchRoom.action',
			data: {
				loginName: loginName
			},
			method: "POST",
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			success: function(res) {
				if (res.data) {
					that.myInfo = res.data;
				}
			},
		});
	},

	methods: {
		open() {
			var that = this;
			var loginName = uni.getStorageSync('loginName');
			uni.showModal({
				title: '提示',
				content: '确认退房？',
				success: function(res) {
					if (res.confirm) {
						uni.request({
							url: 'http://192.168.1.104:8080/gongyv_manage/api/chekoutRoom.action',
							data: {
								loginName: loginName
							},
							method: "POST",
							header: {
								'content-type': 'application/x-www-form-urlencoded'
							},
							success: function(res) {
								if (res.data) {
									that.$refs.popup.open();
									that.myInfo.status = 'WITHDRAW';

									function out() {
										that.$refs.popup.close();

									}
									setTimeout(out, 1500); //   在1500毫秒后只执行一次


								}
							},
						});
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		}
	},
	components: {
		uniCollapse,
		uniCollapseItem,
		uniPopup
	}
}
